import React from "react"
import { graphql } from "gatsby"
import Layout from "../components/layout"
import { css } from "@emotion/react"

export default function About({ data }) {
  return (
    <Layout>
      <h1>About me</h1>
      <p>
        My name is Đỗ Tấn Trung. I'm a Web developer with 1,5 years of experience in Java EE, with good knowledge of Object-Oriented, data structure, and design Patterns.
      </p>
      <div>
        <img alt="TrungDT" src={data.file.publicURL} />
      </div>
      <h3>Education</h3>
      Posts and Telecommunications Institute of Technology
      <div>
        <i css={css`
                    color: #555;
                    font-size: 0.8rem;
                `}>
          2015 - 2020
                </i>
      
      </div>
      <div>&nbsp;</div>
      <h3>Certificate</h3>
      <a href="https://www.youracclaim.com/badges/2280eaa1-155a-4349-8d47-4d4c99e20551">IBM Certified Solution Implementer - API Connect V5.0.5</a>
      <div>&nbsp;</div>
      <h3>Employment History</h3>
      <div><strong>SeatechIt</strong></div>  <i css={css`
                    color: #555;
                    font-size: 0.8rem;
                `}>
        2019 - 2021
                </i>
      <div>
        SeatechIt is a large company in Vietnam specializing in developing and providing software solutions related to finance and banking. During working here I have had experience in ebanking development projects and implementing IBM API connect for banks and financial institutions.
                </div>
    </Layout>
  )
}

export const query = graphql`
  query {
    site {
      siteMetadata {
        title
      }
    },
    file(relativePath: {eq: "resource/avatar.jpg"}) {
        publicURL
    }

  }
`